import Map from "components/Map";
import { useState, useEffect } from "react";
import styles from "./Review.module.css";

interface Reviews {
  reviewId: string;
  building: string;
  newAddress: string;
  oldAddress: string;
  pros: string;
  cons: string;
}
function Review() {
  const [showDetail, setShowDetail] = useState<Boolean>(false);
  const [review, setReview] = useState<any>(null);
  const [reviews, setReviews] = useState<Reviews[]>([
    {
      reviewId: "0001",
      building: "3",
      newAddress: " 34 35",
      oldAddress: " ",
      pros: "  ",
      cons: " ",
    },
    {
      reviewId: "0002",
      building: "  ",
      newAddress: "  34 5-3",
      oldAddress: " ",
      pros: " ",
      cons: " ",
    },
    {
      reviewId: "0003",
      building: "",
      newAddress: "  1 182",
      oldAddress: " ",
      pros: " ",
      cons: " ",
    },
    {
      reviewId: "0004",
      building: "",
      newAddress: "   1675-26",
      oldAddress: " ",
      pros: " ",
      cons: " ",
    },
  ]);

  useEffect(() => {
    if (review !== null) setShowDetail(true);
  }, [review]);
  return (
    <>
      {showDetail ? (
        <>
          <div
            onClick={() => {
              setReview(null);
              setShowDetail(false);
            }}
          >
            
          </div>
          <Map title={review.building} address={review.newAddress} />
          <h1>{review.building}</h1>
          <h3>{review.newAddress}</h3>
          <h3>{review.oldAddress}</h3>
          <h4> : {review.pros}</h4>
          <h4> : {review.cons}</h4>
        </>
      ) : (
        <div className={styles.container}>
          {reviews.map((review) => (
            <div
              className={styles.reviewContainer}
              key={review.reviewId}
              onClick={() => setReview(review)}
            >
              <div>{review.building}</div>
              <div>{review.newAddress}</div>
              <div>{review.oldAddress}</div>
              <div> : {review.pros}</div>
              <div> : {review.cons}</div>
              <div>  ...</div>
            </div>
          ))}
        </div>
      )}
    </>
  );
}
export default Review;
