import { useState } from "react";
import DaumPostcode from "react-daum-postcode";
import styles from "./ReviewFac.module.css";

function ReviewFac() {
  const [openPostcode, setOpenPostcode] = useState(false);
  const [sido, setSido] = useState<string>("");
  const [address, setAddress] = useState("");
  const [oldAddress, setOldAddress] = useState<string>("");
  const [buildingName, setBuildingName] = useState("");
  const [residenceType, setResidenceType] = useState("");
  const [residenceFloor, setResidenceFloor] = useState("");
  const [strength, setStrength] = useState("");
  const [weakness, setWeakness] = useState("");
  const [livedYear, setLivedYear] = useState(0);
  const [star, setStar] = useState(0);
  const [addressTitle, setAddressTitle] = useState("  ");

  let now = new Date();
  let nowYear = now.getFullYear();
  let years = new Array(nowYear - 2020);
  for (let i = 2021; i <= nowYear; i++) {
    years[i - 2021] = i;
  }
  years.reverse();
  /**
   * handler
   */
  const handle = {
    //   
    clickButton: () => {
      setOpenPostcode((current) => !current);
    },

    //   
    selectAddress: (data: any) => {
      setBuildingName(data.buildingName);
      setAddress(data.roadAddress);
      setOldAddress(data.jibunAddress);
      setSido(data.sido);
      setAddressTitle("  ");
      if (data.buildingName == "") setBuildingName(data.roadAddress);
      console.log(`
              : ${data.roadAddress},
              : ${data.zonecode},
               : ${data.jibunAddress},
               : ${data.sido}
          `);
      setOpenPostcode(false);
    },
  };

  const onSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
  };
  const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const {
      target: { name, value },
    } = e;
    if (name === "strength") setStrength(value);
    if (name === "weakness") setWeakness(value);
    console.log(strength, weakness);
  };

  return (
    <div className={styles.container}>
      <form className={styles.form} onSubmit={onSubmit}>
        <label htmlFor="address"></label>
        <div
          id="address"
          className={`${styles.addressInput} ${address == "" || styles.active}`}
          onClick={() => setOpenPostcode(true)}
          title={addressTitle}
        >
          {address == "" ? (
            <>  </>
          ) : (
            <div className={styles.addressInformation}>
              <div>{buildingName}</div>
              <div>{address}</div>
              <div>{oldAddress}</div>
            </div>
          )}
        </div>
        <label></label>
        <div className={styles.buttons}>
          <div
            className={`${styles.mediumBtn} ${
              residenceType == "" && styles.active
            }`}
            onClick={() => setResidenceType("")}
          >
            
          </div>
          <div
            className={`${styles.mediumBtn} ${
              residenceType == "" && styles.active
            }`}
            onClick={() => setResidenceType("")}
          >
            
          </div>
          <div
            className={`${styles.mediumBtn} ${
              residenceType == "//" && styles.active
            }`}
            onClick={() => setResidenceType("//")}
          >
            //
          </div>
        </div>
        <label></label>
        <div className={styles.buttons}>
          <div
            className={`${styles.mediumBtn} ${
              residenceFloor == "" && styles.active
            }`}
            onClick={() => setResidenceFloor("")}
          >
            
          </div>
          <div
            className={`${styles.mediumBtn} ${
              residenceFloor == "" && styles.active
            }`}
            onClick={() => setResidenceFloor("")}
          >
            
          </div>
          <div
            className={`${styles.mediumBtn} ${
              residenceFloor == "" && styles.active
            }`}
            onClick={() => setResidenceFloor("")}
          >
            
          </div>
        </div>
        <label></label>
        <div className={styles.buttons}>
          {years.map((year) => (
            <div
              key={year}
              className={`${styles.mediumBtn} ${
                livedYear == year && styles.active
              }`}
              onClick={() => setLivedYear(year)}
            >
              {year}
            </div>
          ))}
        </div>
        <label></label>
        <input
          value={strength}
          name="strength"
          onChange={onChange}
          placeholder=""
        />
        <label></label>
        <input
          value={weakness}
          name="weakness"
          onChange={onChange}
          placeholder=""
        />
        <label> </label>
        <div className={styles.stars}>
          <div
            className={`${styles.star} ${star >= 1 ? styles.active : null}`}
            onClick={() => setStar(1)}
          >
            
          </div>
          <div
            className={`${styles.star} ${star >= 2 ? styles.active : null}`}
            onClick={() => setStar(2)}
          >
            
          </div>
          <div
            className={`${styles.star} ${star >= 3 ? styles.active : null}`}
            onClick={() => setStar(3)}
          >
            
          </div>
          <div
            className={`${styles.star} ${star >= 4 ? styles.active : null}`}
            onClick={() => setStar(4)}
          >
            
          </div>
          <div
            className={`${styles.star} ${star >= 5 ? styles.active : null}`}
            onClick={() => setStar(5)}
          >
            
          </div>
        </div>
        <div className={styles.submitBtns}>
          <div className={styles.mediumBtn}></div>
          <div className={styles.mediumBtn}></div>
        </div>
      </form>

      <div>
        {openPostcode && (
          <>
            <div
              className={styles.topContainer}
              onClick={() => setOpenPostcode(false)}
            >
                X
            </div>
            <DaumPostcode
              className={styles.daumAddressForm}
              onComplete={handle.selectAddress} //     
              autoClose={false} //     DOM    
              defaultQuery=" 34" //     
            />
          </>
        )}
      </div>
    </div>
  );
}

export default ReviewFac;
